#freelancer-boxed-job-container{
    height: 300px;
    padding: 20px 30px;
    border-bottom: 1px #d5e0d5 solid;
    overflow: hidden;
    cursor: pointer;
}
#freelancer-boxed-job-container:hover{
    background-color: #f2f7f2;
}
#freelancer-boxed-job-container:hover#freelancer-boxed-job-container h2{
    color: #14a800;
}
#freelancer-boxed-job-container h2{
    text-align: center;
    width: 100%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    font-size: 27px;
    color: var(--font-color);
    font-weight: 400 !important;
    margin-bottom: 20px;
}
@media screen and (min-width:772px) {
    #freelancer-boxed-job-container h2 {
        text-align: start;
    }
}
/* .freelancer-boxed-job-description-container{
    max-width: 100%;
    max-height: 80px;
    white-space: nowrap;
    overflow: hidden;
} */
#freelancer-boxed-job-container p{
    color: rgb(85, 84, 84);
    text-transform: capitalize;
    font-size: 14px;
}
#freelancer-boxed-job-container h4{
    width: 100%;
    font-weight: 400 !important;
    font-size: 18px;
    color: var(--font-color);
    text-transform: capitalize;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    margin: 10px 0;
}
.boxed-job-skills-parent{
    width: 100%;
    margin: 25px 0;
    overflow: hidden;
}
.boxed-job-skills-container{
    max-width: 100%;
    height: 100%;
    overflow-x: scroll;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    cursor: pointer;
    /* //user select and scroll missing */
    
}
.boxed-job-skills-container::-webkit-scrollbar{
    display: none;
}