#chat-loader-container{
    height: 100%;
    padding: 20px 30px;
    border-bottom: 1px #d5e0d5 solid;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
@media screen and (min-width:992px) {
    #chat-loader-container{
        margin-left: auto;
        margin-right: auto;
        width: 850px;
    }
}
#chat-loader-container h2{
    text-align: center;
    width: 50%;
    height: 29px;
    background-color: #efefef;
    border-radius: 20px;
    white-space: nowrap;
    overflow: hidden;
    margin-bottom: 25px;
}
#chat-loader-container p{
    width: 65%;
    height: 16px;
    color: rgb(85, 84, 84);
    text-transform: capitalize;
    font-size: 14px;
    background-color: #efefef;
    border-radius: 20px;
    margin-bottom: 20px;
    align-self: flex-start;
}
#chat-loader-container h4{
    align-self: flex-end;
    width: 45%;
    height: 20px;
    background-color: #efefef;
    white-space: nowrap;
    overflow: hidden;
    margin: 25px 0;
    border-radius: 20px;

}
