#freelancer-no-result-container{
    height: 200px;
    padding: 20px 30px;
    border-bottom: 1px #d5e0d5 solid;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
}
.freelancer-no-result-text{
    color: grey;
    text-align: center;
    font-size: 16px;
    /* color: var(--font-color); */
    font-weight: 500;
    margin-left: 20px;
}
.freelancer-no-result-sep{
    color: grey;
    text-align: center;
    font-size: 50px;
    font-weight: 300;
    margin-left: 20px;
}
@media screen and (min-width:772px) {
    .freelancer-no-result-text{
        font-size: 23px;
    }
}