/* header */
.client-job-header{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    display: flex;
}
@media screen and (min-width:992px) {
    .client-job-header{
        justify-content: flex-start;
    }
}
.client-job-logo{
    width:80px;
    height: fit-content;
}
@media screen and (min-width:992px) {
    .client-job-logo{
        width:90px;
    }
}
.client-job-container{
    /* margin: 2rem 2rem 1rem 2rem; */
}
/* post proposal */
.post-prop{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.post-prop .title{
    font-size: 20px;
    font-weight: 500;
    color: var(--font-color);
    margin-top: 16px;
}
.client-proposal-textarea{
    width:90%;
    height: 140px;
    margin-top: 20px;
    border: 1px solid #e4ebe4;
    border-radius: 4px;
    font-size: 16px;
    padding: 8px 12px;
    font-weight: 400;
}
@media screen and (min-width:992px) {
    .client-proposal-textarea{
        width:60%;
    }}
    .client-proposal-submit{
        width:80%;
        height: 45px;
        margin-top: var(--input-margin-spacing);
        color: white;
        background-color: rgb(16 138 0);
        border: none;
        border-radius: 20px;
        font-size: var(--btn-font-size);
        font-weight: 500;
        cursor: pointer;
        letter-spacing: 1px;
    }
    .client-proposal-submit:hover{
        background-color: #14a800;
    }
    @media screen and (min-width:772px) {
        .client-proposal-submit{
            width:50%;
        }}
    @media screen and (min-width:992px) {
        .client-proposal-submit{
            width:30%;
        }}
        .post-prop .informal{
            margin-top: 16px;
            font-size: 16px;
            color: var(--font-color);
            font-weight: 500;
        }
        /* proposals */
        .client-job-proposals{
            margin-top: 1rem;
        }
        .client-job-proposals .proposal-container{
            border-bottom: 1px solid var(--border-color);
            padding-bottom: 1rem;
        }
        .client-job-proposals .title{
            font-size: 20px;
            font-weight: 500;
            color: var(--font-color);
            text-align: center;
        }
        .client-job-proposals .username{
            margin-top: 1rem;
            font-size: 17px;
            font-weight: 500;
            color: #14a800;
            text-transform: capitalize;
        }
        .client-job-proposals .proposal{
            margin-top: .25rem;
            color: var(--font-color);
            white-space: pre-line;
        }
        .client-job-proposals form {
            margin-top: 1rem;
            width: 100%;
            text-align: center;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
        }
        .client-job-proposals form textarea {
            width: 100%;
            border: 1px solid var(--border-color);
            border-radius: 6px;
            padding: 4px 8px;
            font-size: 14px;
        }
        .client-job-proposals form button{
            margin-top: 16px;
            margin-left: 8px;
            width: 25%;
            background-color: rgb(16 138 0);
            color: white;
            font-weight: 500;
            padding: 4px 50px;
            border-radius: 20px;
        }
        .client-job-proposals form button:hover{
            background-color: #14a800;
        }
        @media screen and (min-width:772px) {
            .client-job-proposals{
                padding-right: 2rem;
                padding-left: 2rem;
            }
            .client-job-proposals form textarea {
                width: 70%;
                height: 60px;
            }
            .client-job-proposals form button {
                width: 10%;
            }
        }











