/* header */
.freelancer-job-header{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    display: flex;
}
@media screen and (min-width:992px) {
    .freelancer-job-header{
        justify-content: flex-start;
    }
}
.freelancer-job-logo{
    width:80px;
    height: fit-content;
}
@media screen and (min-width:992px) {
    .freelancer-job-logo{
        width:90px;
    }
}
.freelancer-job-container{
    /* margin: 2rem 2rem 1rem 2rem; */
}
/* post proposal */
.post-prop{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.post-prop .title{
    font-size: 20px;
    font-weight: 500;
    color: var(--font-color);
    margin-top: 16px;
}
.freelancer-proposal-textarea{
    width:90%;
    height: 140px;
    margin-top: 20px;
    border: 1px solid #e4ebe4;
    border-radius: 4px;
    font-size: 16px;
    padding: 8px 12px;
    font-weight: 400;
}
@media screen and (min-width:992px) {
    .freelancer-proposal-textarea{
        width:60%;
    }}
    .freelancer-proposal-submit{
        width:80%;
        height: 45px;
        margin-top: var(--input-margin-spacing);
        color: white;
        background-color: rgb(16 138 0);
        border: none;
        border-radius: 20px;
        font-size: var(--btn-font-size);
        font-weight: 500;
        cursor: pointer;
        letter-spacing: 1px;
    }
    .freelancer-proposal-submit:hover{
        background-color: #14a800;
    }
    @media screen and (min-width:772px) {
        .freelancer-proposal-submit{
            width:50%;
        }}
    @media screen and (min-width:992px) {
        .freelancer-proposal-submit{
            width:30%;
        }}
        .post-prop .informal{
            margin-top: 16px;
            font-size: 16px;
            color: var(--font-color);
            font-weight: 500;
            text-align: center;
        }