.login-container{
    /* margin: 20px; */
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.login-header{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
/* desktop screens */
@media screen and (min-width:992px) {
    .login-header{
        justify-content: flex-start;
    }
}
.login-logo{
    width:80px;
    height: fit-content;
}
@media screen and (min-width:992px) {
    .login-logo{
        width:90px;
    }
}
.login-box{
    width:100%;
    margin-top: 40px;
}
@media screen and (min-width:772px) {
    .login-box{
        width: 60%;
        border: 1px #e7eee7 solid;
        border-radius: 15px;
        padding: 30px;
        padding-left: 120px;
        padding-right: 120px;
        margin-top: 80px;
    }
}
@media screen and (min-width:992px) {
    .login-box{
        width:420px;
    }
}
.login-form{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.login-box-title{
    font-size: var(--font-size-h1);
    font-weight: 500;
    text-align: center;
    color: #001e00;
}
.login-input-container{
    width:100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: var(--input-margin-spacing);
    border: 1px solid #e4ebe4;
    border-radius: var(--form-border-radius);
}
.login-input{
    height: 36px;
    width: 100%;
    padding:0px;
    box-sizing: border-box;
    border: none;
    font-size: 14px;
    font-weight:lighter;
    padding: 12px;
}
@media screen and (min-width:772px) {
    .login-input{
        border-left: 1px solid #e4ebe4;
    }
}
.login-icon-lable{
    width:60px;
    text-align: center;
    background-color: transparent;
    border: none;
}
#loginPassword{
    font-size: 16px;
    font-weight: 500;
    letter-spacing: 2px;
}
#loginShowPassword{
    cursor: pointer;
}
.login-submit{
    width:100%;
    height: 45px;
    margin-top: var(--input-margin-spacing);
    color: white;
    background-color: rgb(16 138 0);
    border: none;
    border-radius: 20px;
    font-size: var(--btn-font-size);
    font-weight: 500;
    cursor: pointer;
    letter-spacing: 1px;
}
.login-submit:hover{
    background-color: #14a800;
}
.login-input:hover::-webkit-input-placeholder{
    color: #cbcbcba8;
}
.login-input:hover:-ms-input-placeholder{
    color: #cbcbcba8;
}
.login-input:hover::placeholder{
    color: #cbcbcba8;
}
::-webkit-input-placeholder { /* Edge */
  color: #cbcbcb;
  transition: 200ms ease-out;
}

:-ms-input-placeholder { /* Internet Explorer 10-11 */
  color: #cbcbcb;
  transition: 200ms ease-out;
}

::placeholder {
  font-size: 14px;
  letter-spacing: normal;
  color: #cbcbcb;
  transition: 200ms ease-out;
}

input:focus{
    outline: none;
}
.login-err{
    color: red;
    text-align: center;
    font-size: smaller;
    font-weight: 500;
    margin-top: 16px;
    visibility: hidden;
}