.pagination-main-container{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 16px;
}
.pagination-main-container button{
    padding: 4px 10px;
    font-size: 18px;
    font-weight: bold;
    color: var(--font-color);
}
.pagination-main-container .navigation-btn{
    background-color: #13544e;
    padding: 0;
    border-radius: 4px;
    color: white;
    font-weight: 500;
    align-items: center;
}
.pagination-main-container .navigation-btn:disabled{
    background-color: #6f8987;
}
.pagination-main-container .navigation-btn:-moz-user-disabled{
    background-color: #6f8987;
}