
.chat-parent{
    /* width: 90%; */
    /* margin: 1rem; */
    margin-bottom: 0px;
    border: 1px solid var(--border-color);
    border-radius: 8px;
    padding: 1rem;
    padding-bottom: 0;
    white-space: nowrap;
    height: 70vh;
    overflow-y: scroll;
    overflow-x: hidden;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    position: relative !important;
}
@media screen and (min-width:992px) {
    .chat-room-container{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
    .chat-parent{
        width: 850px;
    }
}
.chat-room-container .title{
    text-align: center;
    font-size: 16px;
    font-weight: 500;
    color: var(--font-color);
    margin: 4px 16px;
}
@media screen and (min-width:772px) {
    .chat-room-container .title{
        font-size: 20px;
        margin: 4px;
    }
}
.chat-room-container .message{
    width: fit-content;
    color: white;
    font-size: 14px;
    font-weight: 400;
    padding: 4px 10px;
    background-color: blue;
    border-radius: 12px;
    margin: 8px 4px;
    white-space: pre-line;
}
@media screen and (min-width:772px) {
    .chat-room-container .message{
        font-size: 15px;
        padding: 4px 12px;
        border-radius: 14px;
    }
}
.chat-room-container .message-datetime{
    color: lightgrey;
    font-size: 9px;
    font-weight: 300;
    text-align: end;
}
@media screen and (min-width:772px) {
    .chat-room-container .message-datetime{
        font-size: 12px;
    }
}
.send-message-container {
    width:100%;
    padding-top: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    position: sticky;
    bottom: -1px;
    padding-bottom: 16px;
    background-color: white;
    box-sizing: border-box;
}
.send-message-container button{
    width: 50px;
    height: 35px;
    padding:6px;
    background-color: #108a00;
    color: white;
    font-weight: 500;
    font-size: 14px;
    border-radius: 6px;
    box-sizing: border-box;
}
.send-message-container button:hover{
    background-color: #14a800;
}
.send-message-container textarea{
    width: 80%;
    height: 25px;
    border: 1px solid var(--border-color);
    border-radius: 6px;
    margin-left: 12px;
    padding: 5px 12px;
}
@media screen and (min-width:772px) {
    .send-message-container button{
        width: 20%;
        font-size: 16px;
    }
    .send-message-container textarea{
        width:75%
    }
}