.client-feed-container{
    /* margin: 1rem; */
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
/* header */
.freelancer-feed-header{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
@media screen and (min-width:992px) {
    .freelancer-feed-header{
        justify-content: flex-start;
    }
}
.freelancer-feed-logo{
    width:80px;
    height: fit-content;
}
@media screen and (min-width:992px) {
    .freelancer-feed-logo{
        width:90px;
    }
}
/* create job */
.client-feed-container .create-job{
    margin-top: 2rem;
    width: 100%;
    border: 1px solid #d5e0d5;
    border-radius: 8px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: 200ms;
    cursor: pointer;
    box-sizing: border-box;
}
.client-feed-container .create-job:hover{
    border-color: #18a400;
}
.client-feed-container .create-job:hover .text{
    color: #18a400;
}
.client-feed-container .create-job .seperator{
    height: 80px;
    width: 3px;
    background-color: rgb(155, 154, 154);
    border-radius: 4px;
    margin: 0 20px;
}
.client-feed-container .create-job .text{
    color: rgb(155, 154, 154);
    font-size: 18px;
    font-weight: 500;
    text-align: center;
}
@media screen and (min-width:992px) {
    .client-feed-container .create-job{
        width: 50%;
    }
    .client-feed-container .create-job .text{
        font-size: 24px;
    }
    .client-feed-container .create-job .seperator{
        margin: 0 50px;
    }
}
/* feed */
.client-feed-container .client-jobs-feed{
    box-sizing: border-box;
    margin-top: 1rem;
    width: 100%;
    border: 1px solid #d5e0d5;
    border-radius: 8px;
}
@media screen and (min-width:992px) {
    .client-feed-container .client-jobs-feed{
        width: 70%;
    }
}