.new-job-container{
    /* margin: 20px; */
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.new-job-header{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
/* desktop screens */
@media screen and (min-width:992px) {
    .new-job-header{
        justify-content: flex-start;
    }
}
.new-job-logo{
    width:80px;
    height: fit-content;
}
@media screen and (min-width:992px) {
    .new-job-logo{
        width:90px;
    }
}
.new-job-box{
    width:100%;
    margin-top: 40px;
}
@media screen and (min-width:772px) {
    .new-job-box{
        width: 70%;
        border: 1px #e7eee7 solid;
        border-radius: 15px;
        padding: 30px;
        padding-left: 80px;
        padding-right: 80px;
        margin-top: 80px;
    }
}
@media screen and (min-width:992px) {
    .new-job-box{
        width:600px;
        padding-left: 50px;
        padding-right: 50px;
    }
}
.new-job-form{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.new-job-box-title{
    font-size: var(--font-size-h1);
    font-weight: 500;
    text-align: center;
    color: #001e00;
}
.new-job-input-container{
    width:100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
    border: 1px solid #e4ebe4;
    border-radius: 4px;
}
