.new-job-input{
    height: 36px;
    width: 100%;
    padding:0px;
    box-sizing: border-box;
    border: none;
    font-size: 16px;
    font-weight:lighter;
    padding: 12px;
}
.new-job-textarea{
    width:100%;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
    border: 1px solid #e4ebe4;
    border-radius: 4px;
    font-size: 16px;
    padding: 8px 12px;
}


.new-job-submit{
    width:100%;
    height: 45px;
    margin-top: 20px;
    color: white;
    background-color: rgb(16 138 0);
    border: none;
    border-radius: 20px;
    font-size: var(--btn-font-size);
    font-weight: 500;
    cursor: pointer;
    letter-spacing: 1px;
}
.new-job-submit:hover{
    background-color: #14a800;
}
.new-job-input:hover::-webkit-input-placeholder{
    color: #cbcbcba8;
}
.new-job-input:hover:-ms-input-placeholder{
    color: #cbcbcba8;
}
.new-job-input:hover::placeholder{
    color: #cbcbcba8;
}
::-webkit-input-placeholder { /* Edge */
  color: #cbcbcb;
  transition: 200ms ease-out;
}

:-ms-input-placeholder { /* Internet Explorer 10-11 */
  color: #cbcbcb;
  transition: 200ms ease-out;
}

::placeholder {
  font-size: 14px;
  letter-spacing: normal;
  color: #cbcbcb;
  transition: 200ms ease-out;
}

input:focus{
    outline: none;
}
textarea:focus{
    outline: none;
}
.new-job-err{
    color: red;
    text-align: center;
    font-size: smaller;
    font-weight: 500;
    margin-top: 16px;
    visibility: hidden;
}


