#freelancer-loader-container{
    height: 300px;
    padding: 20px 30px;
    border-bottom: 1px #d5e0d5 solid;
    overflow: hidden;
}

#freelancer-loader-container h2{
    text-align: center;
    width: 50%;
    height: 29px;
    background-color: #efefef;
    border-radius: 20px;
    white-space: nowrap;
    overflow: hidden;
    margin-bottom: 25px;
}
@media screen and (min-width:772px) {
    #freelancer-loader-container h2 {
        text-align: start;
    }
}
#freelancer-loader-container p{
    width: 70%;
    height: 16px;
    color: rgb(85, 84, 84);
    text-transform: capitalize;
    font-size: 14px;
    background-color: #efefef;
    border-radius: 20px;
    margin-bottom: 20px;
}
#freelancer-loader-container h4{
    width: 40%;
    height: 20px;
    background-color: #efefef;
    white-space: nowrap;
    overflow: hidden;
    margin: 25px 0;
    border-radius: 20px;

}
.loader-skills-parent{
    width: 80%;
    height: 18px;
    margin: 25px 0;
    overflow: hidden;
    background-color: #efefef;
    border-radius: 20px;
    margin-top: 35px;
}
.loader-skills-container{
    max-width: 100%;
    height: 100%;
    overflow-x: scroll;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    cursor: pointer;
    /* //user select and scroll missing */
    
}
.loader-skills-container::-webkit-scrollbar{
    display: none;
}