
.freelancer-feed-container{
    /* margin: 16px; */
}
/* header */
.freelancer-feed-header{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
@media screen and (min-width:992px) {
    .freelancer-feed-header{
        justify-content: flex-start;
    }
}
.freelancer-feed-logo{
    width:80px;
    height: fit-content;
}
@media screen and (min-width:992px) {
    .freelancer-feed-logo{
        width:90px;
    }
}
/* search */
.freelancer-feed-search{
    width: 100%;
    height: 60px;
    margin-top: 16px;
    margin-bottom: 16px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
@media screen and (min-width:772px) {
    .freelancer-feed-search{
        width:75%
    }
}
.freelancer-feed-search input{
    width: calc(100% - 40px);
    height: 40px;
    box-sizing: border-box;
    border: 2px #e4ebe4 solid;
    border-radius: 8px 0 0 8px;
    border-right: none;
    padding: 12px;
    font-weight: 400;
    font-size: 16px;
    color: var(--font-color);
}
.freelancer-feed-search button{
    width: 40px;
    height: 40px;
    border: none;
    border-radius: 0 8px 8px 0;
    background-color: #108a00;
    cursor: pointer;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
}
.freelancer-feed-search button:hover{
    background-color: #14a800;
}
/* feed */
.freelancer-feed{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
}
.freelancer-feed-jobs-container{
    border: 1px #d5e0d5 solid;
    border-radius: 8px;
    width:100%; 
}
@media screen and (min-width:772px) {
    .freelancer-feed-jobs-container{
        width:75%
    }
}
.freelancer-feed-filter{
    display: none;
    width:22%;
    text-align: center;
}
@media screen and (min-width:772px) {
    .freelancer-feed-filter{
        display: block;
    }
}
.freelancer-feed-filter .sort{
    height: 140px;
    display: flex;
    justify-content: space-evenly;
    align-items: flex-start;
    flex-direction: column;
    overflow: hidden;
    padding: 20px;
    border: 1px #d5e0d5 solid;
    border-radius: 8px;
}
.freelancer-feed-filter .sort .title{
    font-size: 24px;
    color: var(--font-color);
    font-weight: 500;
}
.freelancer-feed-filter .sort button{
    margin-top: 15px;
    background-color: transparent;
    border: none;
    text-decoration: underline;
    cursor: pointer;
    color: #13544e;
    font-size: 15px;
}
.freelancer-feed-filter .sort button:hover{
    color: #14a800;
}
.freelancer-feed-filter .filters{
    display: flex;
    justify-content: space-evenly;
    align-items: flex-start;
    flex-direction: column;
    overflow: hidden;
    padding: 20px;
    border: 1px #d5e0d5 solid;
    border-radius: 8px;
    margin-top: 16px;
}
.freelancer-feed-filter .filters .title{
    font-size: 24px;
    color: var(--font-color);
    font-weight: 500;
}
.freelancer-feed-filter .filters .filter-title{
    margin-top: 16px;
    font-weight: 500;
    color: var(--font-color);
}
.freelancer-feed-filter .filters button{
    width: 35px;
    margin-top: 15px;
    background-color: #13544e;
    height: 25px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    color: white;
    font-size: 15px;
}
.freelancer-feed-filter .filters button:hover{
    background-color: #1f7970;
}

.freelancer-feed-filter .filters input{
    width: calc(100% - 40px);
    height: 25px;
    box-sizing: border-box;
    border: #d5e0d5 2px solid;
    border-radius: 4px;
    padding: 6px;
    color: var(--font-color);
    font-size: 14px;
}
.freelancer-feed-filter .filters .skills-search{
    text-align: start;
}
.freelancer-feed-filter .filters .page-limit{
    text-align: start;
}