.App{
  margin: 2rem 1rem;
}
.header{
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
/* desktop screens */
@media screen and (min-width:992px) {
  .header{
      justify-content: flex-start;
  }
}
.logo{
  width:80px;
  height: fit-content;
}
@media screen and (min-width:992px) {
  .logo{
      width:90px;
  }
}