#freelancer-full-job-container{
    height: fit-content;
    padding: 20px 30px 10px 30px;
    border-bottom: 1px #d5e0d5 solid;
    overflow: hidden;
    text-align: center;
}
#freelancer-full-job-container h2{
    text-align: center;
    width: 100%;
    font-size: 27px;
    color: var(--font-color);
    font-weight: 400 !important;
    margin-bottom: 20px;
}
@media screen and (min-width:772px) {
    #freelancer-full-job-container h2, #freelancer-full-job-container h4, #freelancer-full-job-container p {
        text-align: start;
    }
}
#freelancer-full-job-container p{
    color: rgb(85, 84, 84);
    text-transform: capitalize;
    font-size: 14px;
}
#freelancer-full-job-container h4{
    width: 100%;
    font-weight: 400 !important;
    font-size: 18px;
    color: var(--font-color);
    text-transform: capitalize;
    margin: 10px 0;
}
.full-job-skills-parent{
    width: 100%;
    margin: 25px 0;
    overflow: hidden;
}
.full-job-skills-container{
    max-width: 100%;
    height: 100%;
    overflow-x: scroll;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    /* //user select and scroll missing */
    
}
.full-job-skills-container::-webkit-scrollbar{
    display: none;
}