#client-boxed-job-container{
    height: 245px;
    padding: 20px 30px;
    border-bottom: 1px #d5e0d5 solid;
    overflow: hidden;
    cursor: pointer;
}
#client-boxed-job-container:hover{
    background-color: #f2f7f2;
}
#client-boxed-job-container:hover#client-boxed-job-container h2{
    color: #14a800;
}
#client-boxed-job-container h2{
    text-align: center;
    width: 100%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    font-size: 19px ;
    color: var(--font-color);
    font-weight: 500 ;
    margin-bottom: 20px;
}
@media screen and (min-width:772px) {
    #client-boxed-job-container h2 {
        text-align: start;
        font-size: 27px;
        font-weight: 400;
    }
    #client-boxed-job-container p{
        font-size: 16px;
    }
    #client-boxed-job-container .proposals{
        font-size: 18px;
    }
    #client-boxed-job-container h4{
        font-size: 18px;
    }
}
#client-boxed-job-container p{
    color: rgb(85, 84, 84);
    text-transform: capitalize;
    font-size: 14px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
#client-boxed-job-container .proposals{
    font-size: 15px;
    font-weight: 500;
    color: #13544e;
}
#client-boxed-job-container h4{
    width: 100%;
    font-weight: 400 !important;
    font-size: 16px;
    color: var(--font-color);
    text-transform: capitalize;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    margin: 10px 0;
}
