.nav-bar-container{
    width:100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 1rem;
}
.nav-bar-container div{
    margin-left: 2rem;
    color: #14a800;
    font-size: 16px;
    font-weight: 500;
    background-color: #f2f7f2;
    border: 3px solid #f2f7f2;
    border-radius: 8px;
    padding:4px 8px;
}
.nav-bar-container button{
    background-color: #1f7970;
    color: white;
    margin-right: 2rem;
    padding: 4px 12px;
    border-radius: 8px;
}
@media screen and (max-width:772px) {
    .nav-bar-container div{
        margin-left: 0px;
        font-size: 14px;
        font-weight: 400;
        padding: 4px;
    }
    .nav-bar-container button{
        margin-right: 0;
        margin-left: 8px;
        font-size: 13px;
        padding: 4px 8px;
    }
}